input {
    outline: none;
}

.container-fluid {
    width: 60%;
}

#root {
    max-width: 100%;
    overflow: hidden;
    background: #F4F4F4;
}

p {
    margin: 0;
    font-size: 0.85rem;
    font-family: 'Roboto Font';
}

a {
    text-decoration: none;
    color: #19191A;
    font-family: 'Circe';
    font-style: normal;
    font-weight: normal;
}

a:hover {
    text-decoration: none;
    color: #19191A;
}

a.button {
    background: #005FF9;
    border-radius: 16px;
    font-family: 'Circe';
    font-style: normal;
    font-weight: bold;
    font-size: 17px;
    line-height: 58px;
    text-align: center;
    color: #F2F2F2;
    padding: 1.2rem 5rem;
}

.bold {
    font-family: 'Circe Bold';
}

.extraBold {
    font-family: 'Circe ExtraBold';
}

.blackCard, .grayCard, .whiteCard, .greenCard {
    width: fit-content;
}